      editable: false
    };
  },
  created: function() {
    // `this` points to the vm instance
    console.log("created...");
    var $this = this;
    this.$root.$on("send", function(val) {
      //$this.editable = false;
      $this.close();
      // console.log("on... event.....", val);
      // console.log("this.text", $this.text);
      // console.log("this.runeditkey", $this.runeditkey);
      // console.log("this.currentkey", $this.currentkey);
    });
  },
  computed: {
    value: {
      get: function() {
        return this.text;
